:host {
  display: block;
}
table {
	border-collapse: collapse;
}
table thead td {
	background-color: #54585d;
	color: #ffffff;
	font-weight: bold;
	border: 1px solid #54585d;
}
table tbody td {
	color: #636363;
	border: 1px solid #dddfe1;
  min-width: 60px;
}
table tbody tr {
	background-color: #f9fafb;
}
table tbody tr:nth-child(odd) {
	background-color: #ffffff;
}